.intro {
    background-color: var(--placeholder-1) !important;
}

.intro>div:nth-child(1) {
    background-size: cover !important;
    background-position-x: center !important;
    background-position-y: var(--posY) !important;
    position: relative;
    z-index: 3;
    width: 100%;
    min-height: 350px;
    height: 100%;
        transition: var(--transition) 1s opacity;
}

.intro:not(#sofas-intro)+section {
    --catalog-slider-img-width: clamp(16.25rem, 15.0962rem + 5.1282vw, 21.25rem);
    --catalog-slider-img-height: clamp(10rem, 9.4231rem + 2.5641vw, 12.5rem);
}

#sofas-intro+section {
    --catalog-slider-img-height: clamp(10rem, 8.7019rem + 5.7692vw, 15.625rem) !important;
    --catalog-slider-img-width: clamp(18.75rem, 16.4423rem + 10.2564vw, 28.75rem) !important;
}

.intro p {
    max-width: 724px;
    font-weight: 300 !important;
}

.intro h2 {
    max-width: 900px;
}

.intro .container {
    position: relative;
}

.intro .center-gr::after {
    content: "";
    opacity: 10%;
    background-color: #000000;
    height: 100%;
    width: 100%;
    position: absolute;
    top: 0;
    z-index: -1;
    left: 0;
}

#sofas-intro.intro .center-gr::after,
#chairs-intro.intro .center-gr::after {
    opacity: 30% !important;
}

#reviews-intro.intro .center-gr::after,
#article-intro.intro .center-gr::after {
    opacity: 32%;
}


.intro .mask-layer {
    height: 100%;
    width: 100%;
    position: absolute;
    top: 0;
    left: 0;
    backdrop-filter: blur(1.2px);

}

#reviews-intro .mask-layer,
#article-intro .mask-layer,
#sofas-intro .mask-layer,
#chairs-intro .mask-layer {
    backdrop-filter: blur(0.5px);
}

#curtains-intro .mask-layer,
#frameless-intro .mask-layer {
    backdrop-filter: blur(0.7px);
}